  height: 40px;
  display: block;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__list {
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: var(--text-color);
  font-size: 12px;
  font-weight: 500;
  line-height: var(--header-size);
  text-align: center;
  height: 60px;
